import * as React from 'react';
import {
  JsonML,
  getTagName,
  getChildren,
  getAttributes,
  getText,
} from '@ali/4ever-utils';
import { ConvertFunc } from '@ali/4ever-cangjie';

export default {
  name: 'code',

  match(node: JsonML): boolean {
    return getTagName(node) === 'code';
  },

  convert(node: JsonML, key: number[] = [], convert: ConvertFunc): React.ReactElement {
    const payload = {
      syntax: 'plaintext',
      theme: 'default',
      ...(getAttributes(node) || {}) as { height: number },
    };
    const code = getText(node);
    return (
      <pre>
        <code
          data-syntax={payload.syntax}
          data-theme={payload.theme}
          style={{ height: `${payload.height}px` }}
        >
          {code}
        </code>
      </pre>
    );
  },
};
